import common from '../common/common.js'
export default {
	data() {
		return {
			adds:false,
			work:'',
			show:'false',
			imgs:'',
			warNno:this.warnno,
			picUrl:''
		}
	},
	mounted() {
		this.picUrl=common.picUrl;
	},
	props: {
		warnno:{
			type:String,
			default() {
					return {
					}
				}
		}
	},
	methods: {
		// 获取工作内容
		getwork(e){
			this.work=e.detail.value;
		},
		// 上传图片
		addimg(){
			var that=this;
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: function(res) {
					// console.log(res.tempFilePaths[0])
					let files = res.tempFilePaths[0];
					console.log(files)
					uni.uploadFile({
						url: 'http://39.106.155.211:8080/muqiang/invitation/UploadFiles',
						filePath: files, //将小程序返回的路径上传给服务器
						name: 'file',
						formData: {
							'user': 'test'
						},
						success(res) {
							var res =JSON.parse(res.data);
							that.imgs=res.data.img
							that.show=true;
						},
						fail(res){
							uni.showToast({
								title:'上传失败',
								icon:'none'
							})
						}
					})
				}
			});
		},
		// 子传父值
		add(){
			var repairMan=uni.getStorageSync('data');
			var data={
				repairMan:repairMan,
				warnNo:this.warNno,
				repairReason1:this.work,
				repairPhoto1:this.imgs
			}
			common.getData('/muqiang/invitation/addRepairRecord', data, (res) => {
				if(res.state==1){
					this.$emit('fatherFun',true)
				}else{
					uni.showToast({
						title:res.message,
						icon:'none'
					})
				}
			})
			
		}
	}
}
